.book-list{
    &:not(:last-child){
        margin-bottom: 5px;
    }


    &-image{
        width: 50px;
        height: 50px;
        border-radius: 5px;
        background-image:url('https://images.unsplash.com/photo-1682685796063-d2604827f7b3?ixlib=rb-4.0.3&ixid=M3wxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80');
        background-size: cover;
        background-position: center;
    }

    &-details{
        width: calc(100% - 50px);
        h4{
            width: 100px;
            font-size: 11px;
            text-transform:  uppercase;
        }
    }

    &-inner{
        width: calc(100% - 15px);
        gap: 10px;
    }

    .book-list-genres{
        gap:5px;
        flex-wrap: wrap;
        width: 129px;
        margin-top: 5px;

        .filter-item{
            @include tag;
            @include semiTransparentColouredBorderBox('&.red-outline' , $primaryRed);
            @include semiTransparentColouredBorderBox('&.green-outline' , $primaryGreen);
            @include semiTransparentColouredBorderBox('&.purple-outline' , $primaryPurple);
            @include semiTransparentColouredBorderBox('&.orange-outline' , $primaryOrange);
        }
    }
}